import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Predicciones, PrediccionesMeta, RealTimeData, RealData } from './Data'

@Injectable({
  providedIn: 'root'
})
export class DataService {
  constructor(private http: HttpClient) { }

  // Tiempo real
  cryptovalues = "https://api.coinmarketcap.com/v2/ticker/?limit=100&structure=array"
  globaldata = 'https://api.coinmarketcap.com/v2/global/'

  getRealTimeCryptoValues() {
    return this.http.get<RealTimeData>(this.cryptovalues);
  }

  getGlobalData() {
    return this.http.get(this.globaldata);
  }

  // Predicciones
  //predicciones = './assets/toAngular.json';
  //prediccionesMeta = './assets/PrediccionMeta.json';

  prediccionesBitcoin = './assets/script/bitcoin/toAngular.json';
  prediccionesEthereum = './assets/script/ethereum/toAngular.json';
  prediccionesRipple = './assets/script/ripple/toAngular.json';
  prediccionesBitcoinCash = './assets/script/bitcoin-cash/toAngular.json';
  prediccionesMeta = './assets/script/PrediccionMeta.json';

  getPredicciones(moneda: String) {
    switch (moneda) {
      case 'Bitcoin':
        return this.http.get<Predicciones>(this.prediccionesBitcoin);
      case 'Ethereum':
        return this.http.get<Predicciones>(this.prediccionesEthereum);
        case 'Ripple':
        return this.http.get<Predicciones>(this.prediccionesRipple);
      case 'Bitcoin-Cash':
        return this.http.get<Predicciones>(this.prediccionesBitcoinCash);
    }
    return null;
  }

  getPrediccionesMeta() {
    return this.http.get<PrediccionesMeta>(this.prediccionesMeta);
  }
}
