import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { RealTimeData } from '../Data'
import * as Chart from 'chart.js'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  constructor(private dataService: DataService) {
  }

  realTimeData: any[];
  criptomonedas_activas = 0;
  mercados_activos = 0;
  dominacion_bitcoin = 0;
  market_cap_total = 0;
  vol24h_total = 0;

  ngOnInit() {
    this.dataService.getRealTimeCryptoValues().subscribe((res: RealTimeData) => {
      this.realTimeData = res.data;

      // Configuración de charts
      Chart.defaults.global.defaultFontFamily = "'Roboto', sans-serif";
      Chart.defaults.global.defaultFontColor = "#FFF";
      Chart.defaults.global.defaultFontStyle = 200;
      Chart.defaults.global.defaultFontSize = 14;

      var percent_change_1h: number[] = [];
      var percent_change_24h: number[] = [];
      var percent_change_7d: number[] = [];
      var market_cap: number[] = [];
      var labels: string[] = [];
      var colors = [];
      var borderColors = [];
      for (var i = 0; i < 15; i++) {
        percent_change_1h.push(this.realTimeData[i].quotes.USD.percent_change_1h);
        percent_change_24h.push(this.realTimeData[i].quotes.USD.percent_change_24h);
        percent_change_7d.push(this.realTimeData[i].quotes.USD.percent_change_7d);
        market_cap.push(this.realTimeData[i].quotes.USD.market_cap);
        labels.push(this.realTimeData[i].name);
        colors.push(this.realTimeData[i].quotes.USD.percent_change_24h >= 0 ? 'rgba(0, 255, 0, 0.5)' : 'rgba(255, 0, 0, 0.5)');
        borderColors.push(this.realTimeData[i].quotes.USD.percent_change_24h >= 0 ? 'green' : 'red');
      }
      //Chart Bitcoin
      let myChartBitcoin = new Chart(document.getElementById('chart-change'), {
        type: 'bar',
        data: {
          labels: labels,
          datasets: [{
            fill: true,
            backgroundColor: colors,
            borderColor: borderColors,
            borderWidth: 1.5,
            hoverBorderWidth: 2.5,
            data: percent_change_24h,
          }],
        },
        options: {
          legend: {
            position: 'bottom',
            display: false,
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  suggestedMin: -2,
                  suggestedMax: 2,
                  maxTicksLimit: 10,
                },
                scaleLabel: {
                  display: true,
                  labelString: "% Vol 24h"
                }
              }],
            xAxes: [{
              ticks: {
                autoSkip: false,
                maxRotation: 80
              }
            }]
          }
        }
      })

      //Chart radar
      new Chart(document.getElementById("chart-radar"), {
        type: 'radar',
        data: {
          labels: labels,
          datasets: [
            {
              label: "% Vol 1h",
              fill: true,
              backgroundColor: "rgba(255,0,0,0.1)",
              borderColor: "rgba(255,0,0)", 
              pointBackgroundColor: "rgba(255,0,0)",
              pointBorderColor: "#fff",
              data: percent_change_1h,
              lineTension: -0.1
            },
            {
              label: "% Vol 24h",
              fill: true,
              backgroundColor: "rgba(100,0,255,0.1)", 
              borderColor: "rgba(100,0,255)", 
              pointBackgroundColor: "rgba(100,0,255)",
              pointBorderColor: "#fff",
              data: percent_change_24h,
              lineTension: -0.1
            },
            {
              label: "% Vol 7d",
              fill: true,
              backgroundColor: "rgba(0,0,255,0.1)",
              borderColor: "rgba(0,0,255)", 
              pointBackgroundColor: "rgba(0,0,255)",
              pointBorderColor: "#fff",
              data: percent_change_7d,
              lineTension: -0.1
            }
          ]
        },
        options: {
          legend: {
            position: 'bottom'
          },
          scale: {     
            ticks: {
              suggestedMin: -10,
              suggestedMax: 10,
              maxTicksLimit:10,
              showLabelBackdrop: false,
              fontSize: 14
            },
            pointLabels: {
              fontSize: 15
            }
          }
        }
      });




    });

    this.dataService.getGlobalData().subscribe((res: Response) => {
        this.criptomonedas_activas = res['data'].active_cryptocurrencies;
        this.mercados_activos = res['data'].active_markets;
        this.dominacion_bitcoin = res['data'].bitcoin_percentage_of_market_cap;
        this.market_cap_total = res['data'].quotes.USD.total_market_cap;
        this.vol24h_total = res['data'].quotes.USD.total_volume_24h;
    });
  }
}
